import { motion, useInView } from "framer-motion";
import { useRef, useState, useEffect } from "react";
import { X, Check, Type, MousePointer, PlayCircle, Tv, Gamepad2, Sparkles } from "lucide-react";
import { isMobileDevice, prefersReducedMotion } from "../src/utils/deviceDetect";

const problems = [
  {
    problem: "Hard-to-read tiny text",
    solution: "Smart auto-scaling for perfect readability",
    problemIcon: Type,
    solutionIcon: Tv,
  },
  {
    problem: "Traditional remote-control input offers an unsatisfying browsing experience.",
    solution: "A phone-based touchpad provides highly accurate navigation.",
    problemIcon: MousePointer,
    solutionIcon: Gamepad2,
  },
  {
    problem: "Privacy concerns & hidden trackers",
    solution: "Privacy-first with built-in tracking protection",
    problemIcon: PlayCircle,
    solutionIcon: Sparkles,
  },
];

export function ProblemSolution() {
  const ref = useRef(null);
  const isInView = useInView(ref, { once: true, margin: "-100px" });
  const [isMobile, setIsMobile] = useState(false);
  const [reducedMotion, setReducedMotion] = useState(false);

  useEffect(() => {
    setIsMobile(isMobileDevice());
    setReducedMotion(prefersReducedMotion());
  }, []);

  const animationDuration = isMobile || reducedMotion ? 0.3 : 0.6;

  return (
    <section ref={ref} className="py-32 relative overflow-hidden">
      {/* Background Gradient */}
      <div className="absolute inset-0 bg-gradient-to-b from-transparent via-blue-950/20 to-transparent" />

      <div className="container mx-auto px-6 relative z-10">
        {/* Header */}
        <motion.div
          initial={{ opacity: 0, y: 30 }}
          animate={isInView ? { opacity: 1, y: 0 } : {}}
          transition={{ duration: animationDuration }}
          className="text-center mb-20 will-change-transform"
        >
          <h2 className="text-5xl md:text-6xl font-bold mb-4">
            <span className="bg-gradient-to-r from-red-400 via-white to-cyan-400 bg-clip-text text-transparent">
              Traditional Browsers Weren't Built for TVs
            </span>
          </h2>
          <p className="text-xl text-slate-400 max-w-3xl mx-auto">
            We fixed everything that's broken about browsing on the big screen
          </p>
        </motion.div>

        {/* Problem-Solution Cards */}
        <div className="space-y-8 max-w-5xl mx-auto">
          {problems.map((item, index) => (
            <motion.div
              key={index}
              initial={{ opacity: 0, x: isMobile || reducedMotion ? 0 : (index % 2 === 0 ? -50 : 50) }}
              animate={isInView ? { opacity: 1, x: 0 } : {}}
              transition={{ duration: animationDuration, delay: isMobile || reducedMotion ? index * 0.1 : index * 0.2 }}
            >
              <div className="grid md:grid-cols-2 gap-4">
                {/* Problem */}
                <motion.div
                  whileHover={!isMobile && !reducedMotion ? { scale: 1.02 } : {}}
                  className="relative p-8 rounded-2xl bg-gradient-to-br from-red-950/30 to-slate-900/50 border border-red-500/30 backdrop-blur-sm group"
                >
                  <div className="absolute top-4 right-4">
                    <div className="w-8 h-8 rounded-full bg-red-500/20 flex items-center justify-center">
                      <X className="w-5 h-5 text-red-400" />
                    </div>
                  </div>
                  <div className="text-4xl mb-4">
                    <item.problemIcon className="w-10 h-10 text-red-500" />
                  </div>
                  <p className="text-lg text-slate-300 line-through decoration-red-500/50">
                    {item.problem}
                  </p>
                  <div className="absolute inset-0 bg-gradient-to-br from-red-600/5 to-transparent rounded-2xl opacity-0 group-hover:opacity-100 transition-opacity" />
                </motion.div>

                {/* Solution */}
                <motion.div
                  whileHover={!isMobile && !reducedMotion ? { scale: 1.02 } : {}}
                  className="relative p-8 rounded-2xl bg-gradient-to-br from-cyan-950/30 to-slate-900/50 border border-cyan-400/30 backdrop-blur-sm group"
                >
                  <div className="absolute top-4 right-4">
                    <div className="w-8 h-8 rounded-full bg-cyan-500/20 flex items-center justify-center">
                      <Check className="w-5 h-5 text-cyan-400" />
                    </div>
                  </div>
                  <div className="text-4xl mb-4">
                    <item.solutionIcon className="w-10 h-10 text-cyan-500" />
                  </div>
                  <p className="text-lg text-white font-medium">
                    {item.solution}
                  </p>
                  <div className="absolute inset-0 bg-gradient-to-br from-cyan-600/10 to-transparent rounded-2xl opacity-0 group-hover:opacity-100 transition-opacity" />
                  
                  {/* Glow Effect */}
                  <div className="absolute inset-0 bg-gradient-to-br from-cyan-400/20 to-transparent rounded-2xl blur-xl opacity-0 group-hover:opacity-100 transition-opacity -z-10" />
                </motion.div>
              </div>
            </motion.div>
          ))}
        </div>
      </div>
    </section>
  );
}

export default ProblemSolution;